import MultiStyleText from "~/components/UI/MultiStyleText/MultiStyleText"
import RichTextField from "~/components/UI/RichText/RichText"
import AccountIcon from "~/icons/AccountIcon"
import {useLinksQuery} from "~/queries/LinksQuery"
import {classes} from "~/utilities/classes"
import LinkButton from "../../UI/Button/LinkButton"
import "./PrivateArea.css"

interface PrivateAreaProps extends Omit<React.HTMLAttributes<HTMLDivElement>, "title"> {
    title: string | null | undefined
    content: string | null | undefined
    ctaName: string | null | undefined
    ctaLink: string | null | undefined
}

const PrivateArea: React.VFC<PrivateAreaProps> = props => {
    const {className, children, title, content, ctaName, ctaLink, ...otherProps} = props
    const links = useLinksQuery()

    return (
        <div className={classes("private-area-86pn")} {...otherProps}>
            <div className="icon-container-86pn">
                <div className="badge-86pn" />
                <AccountIcon className="icon-86pn" />
            </div>
            <MultiStyleText flex className="title-86pn" className1="title-part-1-86pn" className2="title-part-2-86pn">
                {title}
            </MultiStyleText>
            <RichTextField className="content-86pn chars-h6-80">{content}</RichTextField>
            <LinkButton className="cta-86pn" to={links.privateArea.url}>
                {ctaName}
            </LinkButton>
        </div>
    )
}

export default PrivateArea
