import {GatsbyImage, IGatsbyImageData} from "gatsby-plugin-image"
import {Children, useState} from "react"
import Fader from "~/components/UI/Fader/Fader"
import RichTextField from "~/components/UI/RichText/RichText"
import Tabs from "~/components/UI/Tabs/Tabs"
import {useLinksQuery} from "~/queries/LinksQuery"
import {classes} from "~/utilities/classes"
import LinkButton from "../../UI/Button/LinkButton"
import "./Products.css"

interface SliderProps extends Omit<React.HTMLAttributes<HTMLDivElement>, "children"> {
    children: React.ReactElement<SlideProps> | Array<React.ReactElement<SlideProps>>
}

interface SliderComponents {
    Slide: React.VFC<SlideProps>
}

const Products: React.FC<SliderProps> & SliderComponents = props => {
    const {className, children, ...otherProps} = props
    const slides = Children.toArray(children) as Array<React.ReactElement<SlideProps>>
    const [selected, setSelected] = useState<number>(0)
    const links = useLinksQuery()

    return (
        <div className={classes("products-z3iz", className)} {...otherProps}>
            <Fader className="fader-text-z3iz" selected={selected} onSelect={setSelected}>
                {slides.map((slide, index) => (
                    <Fader.Slide key={index}>
                        <h3 className="fader-title-z3iz">{slide.props.title}</h3>
                        <RichTextField>{slide.props.description?.raw}</RichTextField>
                        {slide.props.ctaLink && (
                            <LinkButton
                                to={`${links.products.url}#${slide.props.ctaLink}`}
                                color="white"
                                style={{marginTop: "var(--gutter-2)"}}
                            >
                                {slide.props.cta}
                            </LinkButton>
                        )}
                    </Fader.Slide>
                ))}
            </Fader>
            <Tabs
                className="tabs-z3iz"
                tabClass="tab-z3iz"
                tabActiveClass="tab-active-z3iz"
                selected={selected}
                onSelect={setSelected}
            >
                {slides.map((slide, index) => (
                    <Tabs.Tab key={index}>{slide.props.title}</Tabs.Tab>
                ))}
            </Tabs>
            <Fader className="fader-mobile-image-z3iz" selected={selected} onSelect={setSelected}>
                {slides.map((slide, index) => (
                    <Fader.Slide key={index}>
                        {slide.props.phoneImage?.gatsbyImageData && (
                            <GatsbyImage
                                className="mobile-image-z3iz"
                                imgClassName="mobile-image-inner-z3iz"
                                alt={slide.props.phoneImage.title ? slide.props.phoneImage.title : ""}
                                image={slide.props.phoneImage.gatsbyImageData}
                                objectFit="contain"
                            />
                        )}
                    </Fader.Slide>
                ))}
            </Fader>
            <Fader className="right-fader-z3iz" selected={selected} onSelect={setSelected}>
                {slides.map((slide, index) => (
                    <Fader.Slide key={index}>
                        {slide.props.image?.gatsbyImageData && (
                            <GatsbyImage
                                alt={slide.props.image.title ? slide.props.image.title : ""}
                                image={slide.props.image.gatsbyImageData}
                                objectFit="cover"
                                objectPosition="center"
                                className="right-image-z3iz"
                                imgClassName="right-image-inner-z3iz"
                            />
                        )}
                    </Fader.Slide>
                ))}
            </Fader>
        </div>
    )
}

export default Products

interface SlideProps {
    title?: string | null | undefined
    cta?: string | null | undefined
    ctaLink?: string | null | undefined
    description?: {raw?: string | null | undefined} | null | undefined
    image?:
        | {
              gatsbyImageData?: IGatsbyImageData | null | undefined
              title?: string | null | undefined
          }
        | null
        | undefined
    phoneImage?: {title?: string | null; gatsbyImageData?: IGatsbyImageData | null | undefined} | null | undefined
}

const Slide: React.VFC<SlideProps> = props => null

Products.Slide = Slide
