import {GatsbyImage} from "gatsby-plugin-image"
import {useLinksQuery} from "~/queries/LinksQuery"
import {PostItem} from "~/types/PostItem"
import {classes} from "~/utilities/classes"
import LinkButton from "../../UI/Button/LinkButton"
import "./News.css"

interface NewsProps extends React.HTMLAttributes<HTMLDivElement> {}

interface NewsComponents {
    NewsItem: React.VFC<NewsBoxProps & PostItem>
}

const News: React.FC<NewsProps> & NewsComponents = props => {
    const {className, children, ...otherProps} = props

    return (
        <div className={classes("news-b4x2", className)} {...otherProps}>
            {children}
        </div>
    )
}

export default News

interface NewsBoxProps extends Omit<React.HTMLAttributes<HTMLDivElement>, "title"> {
    title: string | null | undefined
    cta: string | null | undefined
}

const NewsBox: React.VFC<NewsBoxProps & PostItem> = props => {
    const {image, title, cta, date, className, category, type, tags, description_short, slug, ...otherProps} = props
    const links = useLinksQuery()

    return (
        <div className={classes("news-box-b4x2", className)} {...otherProps}>
            <div className="image-wrapper-b4x2">
                {image?.gatsbyImageData && image.title && (
                    <GatsbyImage alt={image.title} image={image.gatsbyImageData} className="gatsby-image-b4x2" />
                )}
            </div>
            <div className="text-b4x2">
                <p className="date-b4x2">{date}</p>
                <h6 className="title-b4x2">{title}</h6>
                <p className="description-b4x2">{description_short}</p>
                <LinkButton to={`${links.blog.url}/${slug}`} className="cta-b4x2" color="white">
                    {cta}
                </LinkButton>
            </div>
        </div>
    )
}

News.NewsItem = NewsBox
